import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { BtnSave } from 'components/common/button';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { nhangiayto, papers } from 'dump/data';
import FormSearchStudent from './FormSearchStudent';
import FormInfoReceive from './FormInfoReceive';

const columns = [
  {
    title: 'Số báo danh',
    dataIndex: 'sbd',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'name',
  },
  {
    title: 'Giới tính',
    dataIndex: 'sex',
    key: 'name',
  },
  {
    title: 'Tổng điểm',
    dataIndex: 'total',
    key: 'name',
  },
  {
    title: 'Ngành',
    dataIndex: 'nganh',
    key: 'name',
  },
  {
    title: 'Ngày nhập học',
    dataIndex: 'ngaynh',
    key: 'name',
  },
  {
    title: 'Người tiếp nhận',
    dataIndex: 'nguoitn',
    key: 'name',
  },
  {
    title: 'Người thu tiền',
    dataIndex: 'nguoithutien',
    key: 'name',
  },
  {
    title: 'Số tiền nộp',
    dataIndex: 'sotiennop',
    key: 'name',
  },
];

const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
  },
  getCheckboxProps: record => ({
    disabled: record.name === 'Disabled User', // Column configuration not to be checked
    name: record.name,
  }),
};

const paperColumns = [
  {
    title: 'Bắt buộc',
    dataIndex: 'isRequire',
    key: 'code',
  },
  {
    title: 'Tên giấy tờ',
    dataIndex: 'name',
    key: 'code',
  },
  {
    title: 'Số lượng',
    dataIndex: 'number',
    key: 'code',
  },
];

class ReceiveAdmissionPapersPage extends Component {
  state = {};

  render() {
    return (
      <Fragment>
        <Card title="Nhận giấy tờ nhập học">
          <Row>
            <Col span={24}>
              <FormSearchStudent />
            </Col>
            <Col span={24}>
              <Table
                rowSelection={rowSelection}
                bordered
                dataSource={nhangiayto}
                columns={columns}
              />
            </Col>
          </Row>
        </Card>
        <Card title="Thông tin giấy tờ">
          <Row>
            <Col span={8}>
              <FormInfoReceive />
            </Col>
            <Col span={16}>
              <Row>
                <Table
                  rowSelection={rowSelection}
                  bordered
                  dataSource={papers}
                  columns={paperColumns}
                />
              </Row>
            </Col>
            <Col offset={8} span={12}>
              <BtnSave htmlType="submit" />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReceiveAdmissionPapersPage />
  </Page>
);



