import React, { Component } from 'react';
import { Row, Col, Form, Input, DatePicker, Radio, Checkbox } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';

const RadioGroup = Radio.Group;
const { RangePicker } = DatePicker;
@Form.create({ name: 'FormInfoReceive' })
class FormInfoReceive extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Col span={24}>
          <Form.Item label="Ngày nhập học">
            {getFieldDecorator('nnhh', {})(<DatePicker />)}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Người tiếp nhận">{getFieldDecorator('ntn', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Hệ NH">{getFieldDecorator('hnh', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Trình độ">{getFieldDecorator('td', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Khối thi">{getFieldDecorator('kt', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Ngành đào tạo">{getFieldDecorator('nt', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label=" ">
            {getFieldDecorator('plkh', {})(
              <Checkbox value={1}>Phân lớp khi gọi nhập học</Checkbox>
            )}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label=" ">
            {getFieldDecorator('inp', {})(<Checkbox value={2}>In phiếu khi lưu</Checkbox>)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoReceive;



