import React, { Component } from 'react';
import { Row, Col, Form, Input, DatePicker, Radio } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';

const RadioGroup = Radio.Group;
const { RangePicker } = DatePicker;
@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Số BD">{getFieldDecorator('sbd', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Họ tên">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Khối thi">{getFieldDecorator('kt', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ngành thi">{getFieldDecorator('nt', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Người tiếp nhận">{getFieldDecorator('ntn', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Trình độ">{getFieldDecorator('td', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Hệ NH">{getFieldDecorator('hnh', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Năm NH">{getFieldDecorator('nnh', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ngày nhập học">
              {getFieldDecorator('nnhh', {})(<RangePicker />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Ghi chú">{getFieldDecorator('gt', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label=" ">
              {getFieldDecorator('dn', {})(
                <RadioGroup onChange={this.onChange} value={this.state.value}>
                  <Radio value={1}>Đã nộp</Radio>
                  <Radio value={2}>Chưa nộp</Radio>
                </RadioGroup>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



